/* src/NavBar.css */
.navbar {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #23dfd6;
    padding: 10px 20px;
  }
  .navbar-menu {
    list-style: none;
    display: flex;
    margin: 0;
    padding: 0;
  }
  
  .navbar-menu li {
    margin: 0 15px;
  }
  
  .navbar-menu a {
    text-decoration: none;
    font-size: 18px;
  }
  a:hover {
    background-color: aliceblue;
  }
  

  