.form-b {
    position: absolute; /* Position the element absolutely */
    top: 160px; /* Distance from the top */
    right: 30px; /* Distance from the right */
    padding: 10px;
    background-color: #f4f0ef;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    width: 500px;
    height: 100vh;
}
button {
    border-radius: 10px;
    padding: 5px;
}
input {
    padding: 5px;                /* Padding inside the input */
    border: 1px solid #37a142;       /* Light border */
    border-radius: 5px;          /* Rounded corners for inputs */
    background-color:white;
    margin-bottom: 5px;
}
h5 {
    color: red;
}
button {
    float: right;
    background-color: red;
}
select {
    margin-bottom: 5px; 
}
 

  